<template>
    <div class="mechanism">
        <div class="mechTop">
            <div class="personal">
                <div class="perTop">
                    <p>咨询</p>
                    <span>{{perData.consultation}}</span>
                </div>
                <div class="perTop">
                    <p>服务</p>
                    <span>{{perData.service}}</span>
                </div>
                <div class="perImg">
                    <img :src="perData.img" alt="">
                </div>
                <div class="perTop">
                    <p>关注</p>
                    <span>{{perData.follow}}</span>
                </div>
                <div class="perTop">
                    <p>学习</p>
                    <span>{{perData.study}}</span>
                </div>
            </div>
        </div>
        <div class="perGold">
            <h1>{{name}}</h1>
            <p>金币<span>{{gold}}</span><img src="../assets/gold.svg" alt=""></p>
        </div>
        <div class="mechContent">
            <el-menu style="border: none;display:flex;justify-content:center" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1">资料</el-menu-item>
                <el-menu-item index="2">收藏</el-menu-item>
                <el-menu-item index="3">服务</el-menu-item>
                <el-menu-item index="4">关注</el-menu-item>
                <el-menu-item index="5">课程</el-menu-item>
                <el-menu-item index="6">消息</el-menu-item>
            </el-menu>
            <div class="perContent">
                <perData v-show="perShow"></perData>
                <collection v-show="collection"></collection>
                <serve v-show="serve"></serve>
                <follow v-show="fllow"></follow>
                <curriculum v-show="curriculum"></curriculum>
            </div>
        </div>
    </div>
</template>
<script>
import perData from './personal/personalData'
import collection from './personal/collection'
import follow from './personal/follow'
import serve from './personal/serve'
import curriculum from './personal/curriculum'
export default {
    components: {
        perData,
        collection,
        follow,
        serve,
        curriculum
    },
    data() {
        return{
            perData: {
                consultation: '11',
                service: '1',
                img: require('../assets/personal.png'),
                follow: '123',
                study: '12'
            },
            activeIndex: '1',
            name: '邱振丽',
            gold: '234',
            collection: false,
            perShow: true,
            fllow: false,
            serve: false,
            curriculum: false
        }
    },
    computed: {
    },
    methods: {
        handleSelect(key, keyPath) {
            if (key == 1) {
                this.perShow = true
                this.collection = false
                this.fllow = false
                this.serve = false
                this.curriculum = false
            } else if (key == 2) {
                this.perShow = false
                this.collection = true
                this.fllow = false
                this.curriculum = false
                this.serve = false
            } else if (key == 3) {
                this.perShow = false
                this.collection = false
                this.serve = true
                this.fllow = false
                this.curriculum = false
            } else if (key == 4) {
                this.perShow = false
                this.collection = false
                this.fllow = true
                this.curriculum = false
                this.serve = false
            } else if (key == 5) {
                this.perShow = false
                this.collection = false
                this.fllow = false
                this.curriculum = true
                this.serve = false
            }
        }
    }
}
</script>>
<style scoped>
.mechTop{
    width: 100%;
    height: 315px;
    background-image: url(../assets/mechanism.png);
    background-repeat:no-repeat;
    background-size: cover;
    position: relative;
}
.personal{
    height: 90px;
    margin: 0 10%;
    position: absolute;
    bottom: -20px;
    left: 10%;
    right: 10%;
    display: flex;
    justify-content: center;
    align-items: center
}
.perImg{
    width: 90px;
    height: 90px;
    border-radius: 50%;
    line-height: 90px;
    background-color: #fff
}
.perImg img{
    width: 90%;
    height: 90%;
    display: inline-block;
    vertical-align: middle;
    border-radius: 50%;
}
.personal p{
    text-align: left;
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 14px;
    color: rgba(201, 201, 201, 1);
    margin: 0 35px;
}
.personal span{
    text-align: left;
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 18px;
    color: rgba(250, 250, 250, 1);
    margin: 0 35px;
}
.mechContent{
    padding: 30px 20%;
}
.perGold p{
    font-size: 16px
}
.perGold p span{
    font-size: 14px;
    margin-left: 20px;
    color: red
}
.perGold p img{
    width: 12px;
    height: 12px;
}
.perContent{
    width: 100%;
    background: rgb(248, 248, 248)
}
</style>


