<template>
    <div class="lesson">
        <p class="title">课程上传</p>
        <el-form :label-position="labelPosition" label-width="80px" :model="formData">
            <p>基本信息</p>
            <div class="lessonBox">
                <el-form-item label="课件类型" prop="wechat">
                    <el-radio-group v-model="formData.radio1" size="mini">
                        <el-radio-button label="系列课程"></el-radio-button>
                        <el-radio-button label="视频课程"></el-radio-button>
                        <el-radio-button label="音频课程"></el-radio-button>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="课件名称" prop="email">
                    <el-input size="mini" v-model="formData.email"></el-input>
                </el-form-item>
                <el-form-item label="章节名称" prop="userType">
                    <el-input size="mini" v-model="formData.userType"></el-input>
                </el-form-item>
                <el-form-item label="课时1" prop="remark">
                    <el-input size="mini" v-model="formData.remark"></el-input>
                </el-form-item>
                <el-form-item label="课件上传" prop="remark">
                    <el-upload
                        class="upload-demo"
                        action="https://jsonplaceholder.typicode.com/posts/"
                        multiple
                        :limit="3">
                        <el-button size="small" type="primary">选择视频</el-button>
                        <div slot="tip" class="el-upload__tip">建议上传MP4格式；FLV、AVI格式需要转码，视频最大2G</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="课时2" prop="remark">
                    <el-input size="mini" v-model="formData.remark"></el-input>
                </el-form-item>
                <el-form-item label="课件上传" prop="remark">
                    <el-upload
                        class="upload-demo"
                        action="https://jsonplaceholder.typicode.com/posts/"
                        multiple
                        :limit="3">
                        <el-button size="small" type="primary">选择视频</el-button>
                        <div slot="tip" class="el-upload__tip">建议上传MP4格式；FLV、AVI格式需要转码，视频最大2G</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="" prop="">
                    <el-button style="width: 60%" size="mini">+继续添加章节</el-button>
                    <el-button style="width: 60%;margin-left: 0" size="mini">+继续添加课时</el-button>
                </el-form-item>
                <p>课程信息</p>
                <el-form-item label="课件封面" prop="img">
                    <el-upload
                        class="upload-demo"
                        drag
                        action="https://jsonplaceholder.typicode.com/posts/"
                        multiple>
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">点击添加封面</div>
                        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="课程详情" prop="img">
                    <ue></ue>
                </el-form-item>
            </div>
        </el-form>
    </div>
</template>
<script>
import ue from '../../components/ue'
export default {
    data() {
        return{
            formData: {},
            labelPosition: 'right'
        }
    },
    computed: {
    },
    methods: {
    },
    components: {
        ue
    },
    created () {
    }
}
</script>>
<style scoped>
.lesson .title{
    margin: 0;
    padding: 15px 0;
    border-bottom: 1px solid rgb(254, 145, 80);
    font-size: 14px;
    text-align: center
}
.lessonBox{
    width: 100%;
    text-align: left;
}
.el-input{
    width: 60%
}
.upload-demo{
    text-align: left
}
</style>


