<template>
    <div class="serve">
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            prop="date"
            label="执行环节"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="name"
            label="调解人"
            show-overflow-tooltip
            align="center">
            </el-table-column>
            <el-table-column
            prop="address"
            show-overflow-tooltip
            align="center"
            label="执行现状">
            </el-table-column>
            <el-table-column
            prop="address"
            label="调解信息"
            show-overflow-tooltip
            align="center"
            width="400">
                <template slot-scope="scope">
                    <span>{{scope.row.address}}</span>
                    <el-button type="text" size="small">查看</el-button>
                    <el-button type="text" size="small">下载</el-button>
                    <el-button type="text" size="small">移除</el-button>
                </template>
            </el-table-column>
            <el-table-column
            prop="date"
            label="日期"
            show-overflow-tooltip
            align="center">
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
export default {
    data() {
        return{
            tableData: [{
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄',
                }, {
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄',
                }, {
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }]
        }
    },
    computed: {
    },
    methods: {
    },
    components: {
    },
    created () {
    }
}
</script>>
<style scoped>
.leftTop{
    border-bottom: 1px solid rgb(254, 145, 80)
}
</style>


