</template>
<script>
import media from '../serve/mediation'
import evaluate from '../serve/evaluate'
import apply from '../serve/apply'
export default {
    data() {
        return{
            tab: 'first'
        }
    },
    computed: {
    },
    methods: {
    },
    components: {
        media,
        evaluate,
        apply
    },
    created () {
    }
}
</script>>
<style scoped>
.leftTop{
    border-bottom: 1px solid rgb(254, 145, 80)
}
</style>


