<template>
    <div class="curriculum">
        <el-container>
            <el-aside width="200px">
                <div class="leftTop">全部</div>
                <div class="leftBottom">
                    <!-- <el-menu :default-active="activeIndex" active-text-color='#367be4' :unique-opened='true' class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose">
                        <MenuTree v-for="(item, index) in userMenu" :key="index" :items="item"></MenuTree>
                    </el-menu> -->
                    <el-menu
                        :default-active="activeIndex"
                        class="el-menu-vertical-demo"
                        @select="handleSelect">
                        <el-menu-item v-for="(item, index) in data" :key="index" :index="(index+1).toString()">
                            <i :class="item.icon"></i>
                            <span slot="title">{{item.label}}</span>
                        </el-menu-item>
                    </el-menu>
                </div>
            </el-aside>
            <el-main>
                <lecturer v-show="lecturer"></lecturer>
                <course v-show="course"></course>
                <lesson v-show="lesson"></lesson>
            </el-main>
        </el-container>
    </div>
</template>
<script>
import lecturer from '../curric/lecturer'
import course from '../curric/courseware'
import lesson from '../curric/lesson'
export default {
    data() {
        return{
            data: [{
                label: '讲师审核',
                icon: 'el-icon-menu',
                children: [] 
            },{
                label: '课程上传',
                icon: 'el-icon-menu',
                children: []
            },{
                label: '课件管理',
                icon: 'el-icon-menu',
                children: []
            }],
            activeIndex: '1',
            lecturer: true,
            course: false,
            lesson: false
        }
    },
    computed: {
    },
    methods: {
        handleSelect(key, keyPath) {
            if (key == 1) {
                this.lecturer = true
                this.course = false
                this.lesson = false
            } else if (key == 2) {
                this.lecturer = false
                this.course = false
                this.lesson = true
            } else {
                this.lecturer = false
                this.course = true
                this.lesson = false
            }
        }
    },
    components: {
        lecturer,
        course,
        lesson
    },
    created () {
    }
}
</script>>
<style scoped>
.leftTop{
    border-left: 2px solid rgb(226, 102, 27);
    text-align: left;
    padding-left: 20px;
    background-color: #fff
}
.el-menu-item{
    text-align: left
}
.el-menu{
    padding-top: 0
}
</style>


