<template>
    <div class="apply">
        <p>申请人信息</p>
        <div>
            <tables :message='apply' :show= 'showApply'></tables>
            <tables :message='huanzhe' :show= 'showApply'></tables>
        </div>
        <p>被申请人信息</p>
        <div>
            <tables :message='bapply' :show= 'show'></tables>
        </div>
        <p>纠纷概要</p>
        <div>
            <jtable></jtable>
        </div>
        <el-form :model="stepForm" ref="stepForm" label-width="100px" class="demo-ruleForm" :label-position="labelPosition">
            <div class="stepSi">
                <el-form-item v-for="(item, index) in ListType" :key="index" :label="item" prop="applicant" hide-required-asterisk>
                    <el-upload
                        style="float: left"
                        action="https://jsonplaceholder.typicode.com/posts/"
                        list-type="picture-card"
                        :on-preview="handlePictureCardPreview"
                        :on-remove="handleRemove">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                </el-form-item>
            </div>
        </el-form>
    </div>
</template>
<script>
import tables from '../../components/table'
import jtable from '../../components/jiuTable'
export default {
    data() {
        return{
            apply: "申请人姓名",
            bapply: "被申请人",
            huanzhe: '患者姓名',
            show: true,
            showApply: false,
            labelPosition: 'right',
            stepForm: {},
            dialogImageUrl: '',
            fileList: [],
            dialogVisible: false,
            ListType: ['医疗纠纷材料','授权委托书', '委托人、被委托人相关证件','病例及相关检查资料','医疗费用及其他相关费用票据','误工证明','患者死亡相关证明','新生儿出生证明']
        }
    },
    computed: {
    },
    methods: {
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        }
    },
    components: {
        tables,
        jtable
    },
    created () {
    }
}
</script>>
<style scoped>
p{
    text-align: left;
    font-size: 14px
}
</style>


