<template>
    <div class="personalData">
        <p class="title">个人资料</p>
        <div class="content">
            <el-form :label-position="labelPosition" label-width="80px" :model="formData">
                <div class="noModifiable">
                    <p>个人资料</p>
                    <el-form-item label="姓名" prop="name">
                        <span>{{formData.name}}</span>
                    </el-form-item>
                    <el-form-item label="年龄" prop="name">
                        <span>{{formData.name}}</span>
                    </el-form-item>
                    <el-form-item label="手机号" prop="name">
                        <span>{{formData.name}}</span>
                    </el-form-item>
                    <el-form-item label="证件号码" prop="name">
                        <span>{{formData.name}}</span>
                    </el-form-item>
                </div>
                <div class="cardUpload">
                    <p class="contentTitle">身份证上传</p>
                    <div class="uploadBox">
                        <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload">
                            <img v-if="imageUrl" :src="imageUrl" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <p>证件身份证人像面</p>
                    </div>
                    <div class="uploadBoxt">
                        <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload">
                            <img v-if="imageUrl" :src="imageUrl" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <p>证件身份证国徽面</p>
                    </div>
                </div>
                <div style="float:left"><el-button type="primary">修改</el-button></div>
                <div style="clear:both"></div>
                <div>
                    <el-form-item label="微信" prop="wechat">
                        <el-input style="width:48%;margin-right:4%" size="mini" v-model="formData.wechat"></el-input><el-input style="width:48%" size="mini" v-model="formData.wechat"></el-input>
                    </el-form-item>
                    <el-form-item label="邮箱" prop="email">
                        <el-input size="mini" v-model="formData.email"></el-input>
                    </el-form-item>
                    <el-form-item label="用户类型" prop="userType">
                        <el-input size="mini" v-model="formData.userType"></el-input>
                    </el-form-item>
                </div>
                <div style="clear:both"></div>
            </el-form>
            <el-form :label-position="labelPosition" label-width="80px" :model="formData">
                <div class="modifiable">
                    <p>教育背景</p>
                    <el-form-item style="width:100%" label="学校名称" prop="userType">
                        <el-input size="mini" v-model="formData.userType"></el-input>
                    </el-form-item>
                </div>
                <div class="modifiable">
                    <p>&nbsp;</p>
                    <el-form-item style="width:100%" label="" prop="">
                        <el-button size="mini" type="primary">保存</el-button>
                    </el-form-item>
                    </div>
                <div style="clear:both"></div>
            </el-form>
            <el-form :label-position="labelPosition" label-width="80px" :model="formData">
                <div>
                    <p>工作背景</p>
                    <el-form-item label="所在单位" prop="name">
                        <span>{{formData.name}}</span>
                    </el-form-item>
                    <el-form-item label="职业" prop="name">
                        <span>{{formData.name}}</span>
                    </el-form-item>
                    <el-form-item label="职务" prop="name">
                        <span>{{formData.name}}</span>
                    </el-form-item>
                    <el-form-item label="专业方向" prop="name">
                        <span>{{formData.name}}</span>
                    </el-form-item>
                    <el-form-item label="证件类型" prop="email">
                        <el-input size="mini" v-model="formData.email"></el-input>
                    </el-form-item>
                    <el-form-item label="证件编号" prop="wechat">
                        <el-input style="width:48%;margin-right:4%" size="mini" v-model="formData.wechat"></el-input><el-input style="width:48%" size="mini" v-model="formData.wechat"></el-input>
                    </el-form-item>
                    <el-form-item label="调解补充" prop="applicant" hide-required-asterisk>
                        <el-upload
                            class="upload-demo"
                            drag
                            style="float: left"
                            :file-list="fileList"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            multiple>
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>
                </div>
                <div>
                    <el-button type="primary">修改</el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return{
            labelPosition: 'right',
            formData: {
                name: 'dada'
            },
            imageUrl: '',
            fileList: [],
        }
    },
    computed: {
    },
    methods: {
        handleAvatarSuccess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
            console.log(file)
            const isJPG = file.type === 'image/jpeg';
            const isGIF = file.type === 'image/gif';
            const isJPEG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isBMP = file.type === 'image/bmp';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG && !isGIF && !isJPEG && !isPNG && !isBMP) {
                this.$message.error('上传图片只能是 gif,jpg,jpeg,png,bmp 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        },
    }
}
</script>>
<style scoped>
.personalData .title{
    margin: 0;
    padding: 15px 0;
    border-bottom: 1px solid rgb(254, 145, 80);
    font-size: 14px;
    text-align: center
}
.personalData .content{
    padding: 10px 50px
}
.noModifiable {
    width: 40%;
    float: left;
}
p {
    text-align: left;
    font-weight: 700
}
.contentTitle{
    text-align: center
}
.cardUpload{
    width: 50%;
    float: left;
}
.el-form-item{
    width: 50%;
    margin-bottom: 0;
    text-align: left;
    color: #ccc;
}

.uploadBox{
    width: 48%;
    float: left;
}
.uploadBoxt{
    width: 48%;
    float: right;
}
.avatar-uploader-icon{
    width: 120px;
    height: 90px;
    line-height: 90px;
    border: 1px dashed rgb(56, 148, 255);
    border-radius: 5px
}
.modifiable{
    width: 50%;
    float: left;
}
</style>


